export function checkAppointmentTimeFrame(
  dateOfAppointment,
  initialContactDate,
  appointmentType
) {
  var showBlock = false;
  if (dateOfAppointment && initialContactDate) {
    var msec = dateOfAppointment.getTime() - initialContactDate.getTime();
    var hours = msec / (60000 * 60);
    if (
      (appointmentType === "URGENT" && hours > 24) ||
      (appointmentType === "ACCUTE" && hours > 48) ||
      (appointmentType === "ROUTNE" && hours > 120)
    ) {
      showBlock = true;
    }
  }
  return showBlock;
}

export function getBandIdFromUsername(list, username) {
  let info = list.filter((item) => item.username === username)
  if (info.length > 0) {
    return info[0].id;
  }
  return 0;
}
