import React, { useState, useEffect } from "react";

import { makeStyles, Theme } from "@material-ui/core";
import Grid from "@material-ui/core/Grid";
import Slide from '@material-ui/core/Slide';

interface InfoCardMediumProps {
    img?: string,
    title: string,
    subTitle: string,
    content: string
}

const useStyles = makeStyles((theme: Theme) => ({
    root: {
        minHeight: '90px',
        margin: '10px',
        boxShadow: "0px 3px 3px -2px rgba(0,0,0,0.2), 0px 3px 4px 0px rgba(0,0,0,0.14), 0px 1px 8px 0px rgba(0,0,0,0.12)",
        borderRadius: "4px",
        backgroundColor: "#fff",
        transition: "box-shadow 300ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;",
        height: 'max-content',
        padding: '5px 10px 10px 10px',
        marginTop: '30px',
    },

    imgWrapper: {
        width: '64px',
        height: '64px',
        margin: '0 auto',
        overflow: 'hidden',
        boxShadow: '0 6px 10px 0 rgba(0, 0, 0, 0.14), 0 1px 18px 0 rgba(0, 0, 0, 0.12), 0 3px 5px -1px rgba(0, 0, 0, 0.2)',
        borderRadius: '50%',
        marginTop: '-24px',
    },

    img: {
        width: '100%',
        height: 'auto',
    },

    textAlignRight: {
        textAlign: 'right'
    },


    title: {
        fontSize: '18px',
        color: '#3C4858',
        fontWeight: 'bold',
        [theme.breakpoints.only('xs')]: {
            textAlign: 'center'
        }
    },

    subTitle: {
        fontSize: '14px',
        color: '#6c757d',
        fontWeight: 'bold',
        [theme.breakpoints.only('xs')]: {
            textAlign: 'center'
        }
    },

    content: {
        color: '#999',
        padding: '10px'
    }

}));


const InfoCardMedium: React.FC<InfoCardMediumProps> = ({ img, title, subTitle, content }) => {

    const classes = useStyles();
    const [show, setShow] = useState(false);

    useEffect(() => {
        setShow(true);
    }, [])


    return (
        <Slide direction="left" in={show} mountOnEnter unmountOnExit>
            <div className={classes.root}>
                <Grid container>
                    <Grid item xs={12} sm={2} md={3}>
                        {img ?
                            <a>
                                <div className={classes.imgWrapper}>
                                    <img className={classes.img} src={img} alt="..." />
                                </div>
                            </a> : null}
                    </Grid>
                    <Grid item xs={12} sm={10} md={9}>
                        <div className={classes.title} >{title}</div>
                        <div className={classes.subTitle}>{subTitle}</div>
                    </Grid>

                    <Grid item xs={12}>
                        <div className={classes.content}>
                            {content}
                        </div>
                    </Grid>


                </Grid>
            </div>
        </Slide>
    );
}

export default InfoCardMedium;