import React, { useState } from "react";

import { makeStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';

import { useApplicationState } from '../infra/applicationState/ApplicationState';

import InfoCard from '../components/InfoCard';
import SearchBox from '../components/SearchBox';
import InfoDetailModal from "../components/InfoDetailModal";




interface NebulaProps {
}


const useStyles = makeStyles((theme) => ({

    search: {
        marginTop: '100px'
    }
}));

const Nebula: React.FC<NebulaProps> = () => {
    const classes = useStyles();
    const [open, setOpen] = useState(false);
    const [selectedCard, setSelectedCard] = useState();

    const { globalState } = useApplicationState();

    const handleModal = () => {
        setOpen(!open);
    }

    const handleCardClick = (id: number) => {
        //set the card details info
        setSelectedCard(globalState.bandList[id - 1]);
        handleModal();
    }

    return (
        <>
            <SearchBox className={classes.search} />
            <InfoDetailModal open={open} handleClose={handleModal} info={selectedCard} />

            <Grid container>
                {
                    globalState.bandList.map((item: any, index: number) => (
                        <Grid key={`item_${index}`} item xs={12} sm={6} md={4} lg={3}>
                            <InfoCard img={item.logo}
                                id={item.id}
                                title={item.name}
                                subTitle={item.genre}
                                content={item.highlight}
                                handleClick={handleCardClick}
                            />
                        </Grid>
                    ))}
            </Grid>


        </>
    );
}

export default Nebula;
