import React from "react";
import { useHistory } from "react-router-dom";

import { makeStyles, Theme } from "@material-ui/core";

import Dialog from '@material-ui/core/Dialog';
import DialogContent from '@material-ui/core/DialogContent';
import Grid from '@material-ui/core/Grid';
import Button from '@material-ui/core/Button';

import ExploreIcon from '@material-ui/icons/Explore';
import AddCircleIcon from '@material-ui/icons/AddCircle';
import OndemandVideoIcon from '@material-ui/icons/OndemandVideo';

import InfoCardSmall from './InfoCardSmall';


interface InfoDetailModalProps {
    open: boolean,
    info: any,
    handleClose: () => void
}

const useStyles = makeStyles((theme: Theme) => ({
    modal: {

    },

    imgWrapper: {
        margin: 'auto',
        //marginTop: '-80px',
        overflow: 'hidden',
        maxWidth: '130px',
        boxShadow: ' 0 16px 38px -12px rgba(0, 0, 0, 0.56), 0 4px 25px 0px rgba(0, 0, 0, 0.12), 0 8px 10px -5px rgba(0, 0, 0, 0.2)',
        maxHeight: '130px',
        borderRadius: '50%',
    },

    img: {
        width: '100%',
        height: 'auto',
    },

    btn: {
        margin: '10px',
        minWidth: '100px'
    },

    btnWrapper: {
        textAlign: 'right',
        [theme.breakpoints.only('xs')]: {
            textAlign: 'center',
        }
    },

    title: {
        fontSize: '26px',
        fontWeight: 'bold',
        color: '#3C4858',
        padding: '10px',
    },

    subTitle: {
        color: '#6c757d',
        fontWeight: 'bold',
        fontSize: '20px',
        padding: '10px',
    },

    p: {
        color: '#999',
        fontSize: '14px',
        padding: '0 10px',
    },

    hightlightImage: {
        width: '100%',
        boxShadow: '0 5px 15px -8px rgba(0, 0, 0, 0.24), 0 8px 10px -5px rgba(0, 0, 0, 0.2)',
        borderRadius: '6px',
        margin: '20px 0'
    },

    ul: {
        paddingTop: '20px',
        listStyle: 'none',
        [theme.breakpoints.only('xs')]: {
            paddingLeft: '0px',
            textAlign: 'center'
        }
    },

    li: {
        padding: '5px 0',
        fontSize: '16px',
        color: ' #3c4858'
    },

    cursor: {
        cursor: 'pointer'
    }

}));




const InfoDetailModal: React.FC<InfoDetailModalProps> = ({ info, open, handleClose }) => {
    const classes = useStyles();

    const history = useHistory();

    if (info === undefined || info === "undefined")
        return null;


    function handleExplore() {
        history.push(`/${info.username}`);
    }

    return (
        <div>
            <Dialog
                open={open}
                onClose={handleClose}
                scroll='body'
                maxWidth="md"
            >
                <DialogContent className={classes.modal}>
                    <Grid container>
                        <Grid item xs={12} sm={3} lg={2}>
                            <div className={classes.imgWrapper}>
                                <a>
                                    <img className={classes.img} src={info.logo} alt="..." />
                                </a>
                            </div>
                        </Grid>
                        <Grid item xs={12} sm={6} lg={7} >
                            <div className={classes.title}>{info.name}</div>
                            <div className={classes.subTitle}>{info.highlight}</div>
                        </Grid>

                        <Grid item xs={12} sm={3} lg={3} className={classes.btnWrapper}>
                            <Button className={classes.btn} size="small" variant="contained" color="primary"
                                startIcon={<ExploreIcon />}
                                onClick={handleExplore}
                            >Explore</Button>

                            <Button className={classes.btn} size="small" variant="contained" color="primary"
                                startIcon={<AddCircleIcon />}
                            >Follow</Button>
                        </Grid>

                        <Grid item xs={12} md={8}>
                            <Grid container>
                                <Grid item xs={12}>
                                    <p className={classes.p}>{info.description}</p>
                                </Grid>
                                <Grid item xs={12} sm={8}>
                                    <img className={classes.hightlightImage} src="https://demos.creative-tim.com/material-kit-pro-react/static/media/card-project1.6857ca8c.jpg" alt="..." />
                                </Grid>
                                <Grid item xs={12} sm={4}>
                                    <ul className={classes.ul}>
                                        <li className={classes.li}><b>60</b> Gigs</li>
                                        <li className={classes.li}><b>20</b> Venues</li>
                                        <li className={classes.li}><b>331</b> Followers</li>
                                        <li className={classes.li}><b>100</b> Likes</li>
                                    </ul>

                                    <div style={{ padding: '20px', textAlign: 'center' }}>
                                        <div style={{ color: '#3c4858' }}><b>Intro Video</b></div>
                                        <OndemandVideoIcon style={{ fontSize: '80px' }} />
                                    </div>
                                </Grid>
                            </Grid>

                        </Grid>
                        <Grid item xs={12} md={4}>
                            <InfoCardSmall title="Lyanna Mormont" subTitle="Guitarist"
                                img="https://pyxis.nymag.com/v1/imgs/de7/3ef/95cf15c1a5fe0f044922cd8a0ebb4c662d-Bella-Ramsey.rsquare.w700.jpg" />


                            <InfoCardSmall title="Petyr Baelish" subTitle="Keyboard / Vocals" mirror
                                img="https://vignette.wikia.nocookie.net/gameofthrones/images/9/9f/Profile-Littlefinger.png/revision/latest/top-crop/width/360/height/360?cb=20170826005231" />

                            <InfoCardSmall title="Tyrion Lannister" subTitle="Vocals"
                                img="https://vignette.wikia.nocookie.net/gameofthrones/images/9/95/HandoftheKingTyrionLannister.PNG/revision/latest/top-crop/width/360/height/360?cb=20190520175204" />


                        </Grid>
                    </Grid>
                </DialogContent>
            </Dialog>
        </div >
    )
}
export default InfoDetailModal;