import React, { useState } from "react";
import ReactPlayer from "react-player";

import { makeStyles, Theme } from "@material-ui/core";

import Fade from '@material-ui/core/Fade';

import HighlightOffIcon from '@material-ui/icons/HighlightOff';

interface VideoPlayerProps {
    open: boolean,
    info: any,
    handleClose: () => void
}

const useStyles = makeStyles((theme: Theme) => ({
    modal: {
        width: '100%',
        height: '100%',
        background: 'rgb(0,0,0,0.4)',
        position: 'fixed',
        top: '0',
        left: '0',
        cursor: 'pointer',
        zIndex: 5
    },

    container: {
        position: 'fixed',
        top: '0',
        left: '0',
        width: '100%',
        height: '100%',
        zIndex: 1110
    },

    playerWrapper: {
        position: 'relative',
        //  paddingTop: '56.25% '/* Player ratio: 100 / (1280 / 720) */,
        maxWidth: '850px',
        maxHeight: '480px',
        paddingTop: '45%',
        top: '50%',
        left: '50%',
        transform: 'translate(-50%,-50%)',

        [theme.breakpoints.up('lg')]: {
            top: '50%',
            transform: 'translate(-50%,-40%)',

        }
    },

    reactPlayer: {
        position: 'absolute',
        top: '0',
        left: '0',
        zIndex: 10,
        maxWidth: '850px',
        maxHeight: '480px',
    },

    closeIcon: {
        top: '0px',
        position: 'absolute',
        zIndex: 15,
        color: '#fff',
        right: 0,
        fontSize: '30px',
        cursor: 'pointer'
    }

}));




const VideoPlayer: React.FC<VideoPlayerProps> = ({ info, open, handleClose }) => {
    const classes = useStyles();

    const [isReady, setReady] = useState(false);

    if (info === undefined || info === "undefined")
        return null;

    if (open) {
        document.body.style.overflow = 'hidden';
    } else {
        document.body.style.overflow = 'auto';
    }


    return (
        <div>
            <Fade in={open} timeout={300}>
                <div className={classes.modal}> </div>
            </Fade>
            {open ?
                <div className={classes.container} onClick={handleClose}>
                    <div className={classes.playerWrapper}>
                        {isReady ?
                            <HighlightOffIcon className={classes.closeIcon} onClick={handleClose} /> : null}

                        <ReactPlayer className={classes.reactPlayer}
                            width='100%' height='100%'
                            url={info.url}
                            controls
                            onReady={() => setReady(true)}
                        />
                    </div>
                </div> : null}
        </div >
    )
}
export default VideoPlayer;