import React, { useState, useRef } from "react";
import clsx from 'clsx';
import InstagramEmbed from "react-instagram-embed";

import { makeStyles, Theme } from "@material-ui/core";
import Grid from "@material-ui/core/Grid";

import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import ExpandLessIcon from '@material-ui/icons/ExpandLess';

interface InstaPostContainerProps {
    list: any
}

const useStyles = makeStyles((theme: Theme) => ({

    root: {
        boxShadow: "0px 3px 3px -2px rgba(0,0,0,0.2), 0px 3px 4px 0px rgba(0,0,0,0.14), 0px 1px 8px 0px rgba(0,0,0,0.12)",
        borderRadius: "4px",
        backgroundColor: "#fff",
        transition: "box-shadow 300ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;",
        margin: "20px 10px 40px 10px",
        height: 'max-content',
        padding: '20px 0',
        position: 'relative',
    },

    instaPost: {
        padding: '10px 0'
    },

    pulldown: {
        position: 'absolute',
        bottom: '-26px',
        textAlign: 'center',
        width: '100%',
    },

    pullDownIcon: {
        boxShadow: "0px 3px 3px -2px rgba(0,0,0,0.2), 0px 3px 4px 0px rgba(0,0,0,0.14), 0px 1px 8px 0px rgba(0,0,0,0.12)",
        transition: "box-shadow 300ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;",
        color: '#999',
        fontSize: '40px',
        cursor: 'pointer',
        border: '1px solid transparent',
        borderRadius: '50%',
        background: '#f6f6f6',
        '&:hover': {
            color: '#6c757d',
        }
    },

    pullUpIcon: {
        boxShadow: "0px 3px 3px -2px rgba(0,0,0,0.2), 0px 3px 4px 0px rgba(0,0,0,0.14), 0px 1px 8px 0px rgba(0,0,0,0.12)",
        transition: "box-shadow 300ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;",
        color: '#999',
        fontSize: '40px',
        cursor: 'pointer',
        border: '1px solid transparent',
        borderRadius: '50%',
        background: '#f6f6f6',
        position: 'sticky',
        bottom: '0px',

        '&:hover': {
            color: '#6c757d',
        }
    },

    stickyIcon: {
        marginRight: '10px',
        marginTop: '-22px',
        background: 'rgba(246,246,246,0.75)',
        float: 'right'
    },

}));


const InstaPostContainer: React.FC<InstaPostContainerProps> = ({ list }) => {

    const classes = useStyles();
    const instaInit = list.length < 3 ? list.length : 3;
    const [instaListSize, setInstaListSize] = useState(instaInit);
    const ref = useRef<HTMLDivElement | null>(null);

    const items = [];

    const handlePulldown = () => {
        if (instaListSize + instaInit <= list.length) {
            setInstaListSize(instaListSize + instaInit);
        } else {
            setInstaListSize(list.length);
        }
    }

    const handlePullup = () => {
        setInstaListSize(instaInit);
        if (ref.current) {
            window.scrollTo(0, ref.current.offsetTop - 60);
        }
    }

    for (let i = 0; i < instaListSize; i++) {
        let item = list[i];
        items.push(
            <Grid className={classes.instaPost} xs={12} md={6} lg={4}>
                <InstagramEmbed
                    url={item.url}
                    maxWidth={320}
                    hideCaption={true}
                    containerTagName='div'
                    injectScript={false}
                    protocol=''
                    onLoading={() => { }}
                    onSuccess={() => { }}
                    onAfterRender={() => { }}
                    onFailure={() => { }}
                />
            </Grid>)
    }

    return (
        <div ref={ref} className={classes.root}>
            <Grid container>

                {items}
            </Grid>
            <div className={classes.pulldown}>
                {(list.length === items.length) ?
                    <ExpandLessIcon className={classes.pullUpIcon} onClick={handlePullup} /> :
                    <ExpandMoreIcon className={classes.pullDownIcon} onClick={handlePulldown} />
                }
            </div>

            {(items.length !== instaInit && items.length !== list.length) ?
                <ExpandLessIcon className={clsx(classes.pullUpIcon, classes.stickyIcon)} onClick={handlePullup} /> : null}
        </div>
    );
}

export default InstaPostContainer;