import React from 'react';
import clsx from 'clsx';
import { makeStyles, Theme } from "@material-ui/core";
import Input from '@material-ui/core/Input';
import Collapse from '@material-ui/core/Collapse';
import ExpandLessIcon from '@material-ui/icons/ExpandLess';
import SearchIcon from '@material-ui/icons/Search';


interface SearchBoxProps {
    className?: string
}



const useStyles = makeStyles((theme: Theme) => ({
    root: {
        boxShadow: "0px 3px 3px -2px rgba(0,0,0,0.2), 0px 3px 4px 0px rgba(0,0,0,0.14), 0px 1px 8px 0px rgba(0,0,0,0.12)",
        borderRadius: "30px",
        backgroundColor: "#fff",
        transition: "box-shadow 300ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;",
        margin: "10px 10px 20px 10px",
        height: 'max-content'
    },



    searchWrapper: {
        padding: "10px 15px",
        minHeight: "60px",
        display: 'flex'
    },

    searchIcon: {
        margin: 'auto',
        marginRight: '10px',
        color: '#B3B3B3'
    },

    body: {
        padding: "10px 50px",
    },

    dropdown: {
        width: '35px',
        color: '#B3B3B3',
        cursor: 'pointer',
        transition: theme.transitions.create(["transform"], {
            duration: theme.transitions.duration.short
        })
    },

    dropdownOpen: {
        transform: "rotate(-180deg)"
    },

    dropdownClosed: {
        transform: "rotate(0)"
    },
}));



const SearchBox: React.FC<SearchBoxProps> = ({ className }) => {
    const classes = useStyles();

    const [open, setOpen] = React.useState(false);

    let xsStyle = "calc(" + (Math.round(1 * 10e7) / 10e5) + "% - 20px)";
    // Close to the bootstrap implementation:
    // https://github.com/twbs/bootstrap/blob/8fccaa2439e97ec72a4b7dc42ccc1f649790adb0/scss/mixins/_grid.scss#L41
    // subtracted 20px margin


    const showHide = () => {
        setOpen(!open);
    }

    return (
        <div style={{ width: xsStyle }} className={clsx(classes.root, className)}>
            <div className={classes.searchWrapper} >
                <SearchIcon className={classes.searchIcon} />
                <Input type="search" fullWidth
                    placeholder="Search Band, Artist"
                />
                <ExpandLessIcon fontSize="large" className={
                    clsx(classes.dropdown, open ? classes.dropdownClosed : classes.dropdownOpen)
                } onClick={showHide} />

            </div>
            <Collapse in={open}>
                <div className={clsx(classes.body)}>
                    Advance Search
                </div>
            </Collapse>

        </div>

    );
};

export default SearchBox;