import React, { useState, useEffect, Ref } from "react";
import clsx from 'clsx';

import { makeStyles, Theme } from "@material-ui/core";
import Slide from "@material-ui/core/Slide";
import RadioButtonUncheckedIcon from '@material-ui/icons/RadioButtonUnchecked';
import NavigateNextIcon from '@material-ui/icons/NavigateNext';
import NavigateBeforeIcon from '@material-ui/icons/NavigateBefore';



interface QuickNavProps {
    instaRef: any,
    videoRef: any,
    venueRef: any,
    testimonialRef: any,
}

const useStyles = makeStyles((theme: Theme) => ({

    root: {
        zIndex: 15,
        overflow: 'hidden',
        position: 'fixed',
        top: 60,
        height: '350px',
        background: 'rgba(255, 255, 255,0.80)',
        width: '190px',
        right: '0px',
        borderTopLeftRadius: '4px',
        borderBottomLeftRadius: '4px'
    },

    scrollDiv: {
        padding: '0 10px',
        overflow: 'auto',
        width: '100%',
        height: '100%',
    },

    summaryTitleWrapper: {
        padding: '0 10px',
        cursor: 'pointer'
    },

    summaryTitle: {
        paddingTop: '10px',
        paddingBottom: '15px',
        fontSize: '24px',
        letterSpacing: '0.24px',
        color: '#3E3E3E',
        fontWeight: 'bold',
        width: "calc(100% - 24px)",
        float: "left",
    },

    close: {
        marginTop: '5px',
        color: '#999',
        fontSize: '30px'
    },

    summary: {
        border: '1px solid #bdbdbd',
        borderRight: 'none',
        borderTopLeftRadius: '20px',
        borderBottomLeftRadius: '20px',
        color: '#fff',
        cursor: 'pointer',
        position: 'fixed',
        top: '120px',
        right: '0px',
        height: '32px',
        background: 'rgb(0,0,0,0.4)',
    },

    step: {
        display: 'flex',
        width: '100%'
    },

    stepIcon: {
        width: '24px',
        color: '#4165EB',
    },

    stepIconDisabled: {
        width: '24px',
        color: '#D3D3D3',
        cursor: 'pointer'
    },

    stepHead: {
        paddingLeft: '20px',
        fontSize: '16px',
        lineHeight: '24px',
        letterSpacing: '0.24px',
        color: '#3e3e3e',
        cursor: 'pointer',
        '&:hover': {
            color: '#999'
        }
    },

    bold: {
        fontWeight: 'bold'
    },

    editIcon: {
        color: '#B3B3B3',
        fontSize: '16px',
        marginLeft: '10px',
        marginTop: '3px',
        cursor: 'pointer',
    },

    stepBody: {
        minHeight: '30px',
        margin: '5px 0px 5px 11px',
        borderLeft: '1px solid #bdbdbd',
        paddingLeft: '32px',
        paddingBottom: '10px'
    },

    label: {
        color: '#3e3e3e',
        fontWeight: 'bold',
    },

    summaryData: {
        paddingTop: '10px',
        paddingBottom: '10px'
    },

    noBorder: {
        border: "1px solid transparent"
    },

    imageWrapper: {
        display: 'block',
        width: '100%',
        height: '50%',
        overflow: 'hidden',
    },

}));

const scrollToRef = (ref: any) => {
    if (ref.current) {
        window.scrollTo(0, ref.current.offsetTop - 60);
    }
}

const INTRODUCTION = 'INTRODUCTION',
    INSTAGRAM = 'INSTAGRAM',
    VIDEO = 'VIDEO',
    VENUES = 'VENUES',
    TESTIMONIAL = 'TESTIMONIAL';

const QuickNav: React.FC<QuickNavProps> = ({ instaRef, videoRef, venueRef, testimonialRef }) => {
    const classes = useStyles();

    const [open, setOpen] = useState(false);
    const [section, setSection] = useState(INTRODUCTION);

    const showHide = () => {
        setOpen(!open);
    }


    const scrollListener = () => {
        let scrollY = window.scrollY;
        if ((window.innerHeight + window.scrollY) >= document.body.offsetHeight ||
            testimonialRef && scrollY + 65 > testimonialRef.current.offsetTop) {
            setSection(TESTIMONIAL);
        } else if (venueRef && scrollY + 65 > venueRef.current.offsetTop) {
            setSection(VENUES);
        } else if (videoRef && scrollY + 65 > videoRef.current.offsetTop) {
            setSection(VIDEO);
        } else if (instaRef && scrollY + 65 > instaRef.current.offsetTop) {
            setSection(INSTAGRAM);
        } else {
            setSection(INTRODUCTION);
        }
    }

    useEffect(() => {
        window.addEventListener("scroll", scrollListener);
        return () => {
            window.removeEventListener("scroll", scrollListener);
        };
    }, []);

    return (
        <div >
            {!open ?
                <div className={classes.summary} onClick={showHide}>
                    <NavigateBeforeIcon style={{ fontSize: '30px' }} />
                </div> : null
            }
            <Slide direction="left" in={open} mountOnEnter unmountOnExit>
                <div className={classes.root}>
                    <div className={classes.summaryTitleWrapper} onClick={showHide}>
                        <NavigateNextIcon className={classes.close} />
                    </div>
                    <div className={classes.scrollDiv}>
                        <div>
                            <div className={classes.step} onClick={() => window.scrollTo(0, 0)}>
                                <RadioButtonUncheckedIcon className={section === INTRODUCTION ? classes.stepIcon : classes.stepIconDisabled} />
                                <div className={section === INTRODUCTION ? clsx(classes.stepHead, classes.bold) : classes.stepHead}>Introduction</div>
                            </div>
                            <div className={classes.stepBody} />
                        </div>
                        <div>
                            <div className={classes.step} onClick={() => scrollToRef(instaRef)}>
                                <RadioButtonUncheckedIcon className={section === INSTAGRAM ? classes.stepIcon : classes.stepIconDisabled} />
                                <div className={section === INSTAGRAM ? clsx(classes.stepHead, classes.bold) : classes.stepHead}>Instagram Posts</div>
                            </div>
                            <div className={classes.stepBody} />
                        </div>
                        <div>
                            <div className={classes.step} onClick={() => scrollToRef(videoRef)}>
                                <RadioButtonUncheckedIcon className={section === VIDEO ? classes.stepIcon : classes.stepIconDisabled} />
                                <div className={section === VIDEO ? clsx(classes.stepHead, classes.bold) : classes.stepHead}>Youtube Videos</div>
                            </div>
                            <div className={classes.stepBody} />
                        </div>
                        <div>
                            <div className={classes.step} onClick={() => scrollToRef(venueRef)}>
                                <RadioButtonUncheckedIcon className={section === VENUES ? classes.stepIcon : classes.stepIconDisabled} />
                                <div className={section === VENUES ? clsx(classes.stepHead, classes.bold) : classes.stepHead}>Gig Venues</div>
                            </div>
                            <div className={classes.stepBody} />
                        </div>
                        <div>
                            <div className={classes.step} onClick={() => scrollToRef(testimonialRef)}>
                                <RadioButtonUncheckedIcon className={section === TESTIMONIAL ? classes.stepIcon : classes.stepIconDisabled} />
                                <div className={section === TESTIMONIAL ? clsx(classes.stepHead, classes.bold) : classes.stepHead}>Testimonial</div>
                            </div>
                            <div className={clsx(classes.stepBody, classes.noBorder)} />
                        </div>
                    </div>
                </div>
            </Slide>

        </div >
    )
}
export default QuickNav;