import React, { useRef, Ref } from "react";
import { useParams } from "react-router-dom";
import clsx from 'clsx';

import { makeStyles, Theme } from "@material-ui/core";

import Grid from '@material-ui/core/Grid';

import AddCircleIcon from '@material-ui/icons/AddCircle';
import InstagramIcon from '@material-ui/icons/Instagram';
import YouTubeIcon from '@material-ui/icons/YouTube';
import FacebookIcon from '@material-ui/icons/Facebook';


import { useApplicationState } from '../infra/applicationState/ApplicationState';

import InfoCardMedium from '../components/InfoCardMedium';
import InstaPostContainer from '../components/InstaPostContainer';
import VideoPostContainer from '../components/VideoPostContainer';
import TestimonialContainer from '../components/TestimonialContainer';

import { getBandIdFromUsername } from '../util/CommonUtil';

import { instaPostList, videoList, testimonialList, venueList } from '../TempConstants';
import VenuePlayedContainer from "../components/VenuePlayedContainer";
import QuickNav from "../components/QuickNav";


interface ProfileProps {

}

const useStyles = makeStyles((theme: Theme) => ({
    modal: {

    },

    imgWrapper: {
        margin: 'auto',
        //marginTop: '-80px',
        overflow: 'hidden',
        maxWidth: '130px',
        boxShadow: ' 0 16px 38px -12px rgba(0, 0, 0, 0.56), 0 4px 25px 0px rgba(0, 0, 0, 0.12), 0 8px 10px -5px rgba(0, 0, 0, 0.2)',
        maxHeight: '130px',
        borderRadius: '50%',
    },

    img: {
        width: '100%',
        height: 'auto',
    },

    btn: {
        margin: '0px 10px',
        minWidth: '100px'
    },

    btnWrapper: {
        textAlign: 'center',
        [theme.breakpoints.up('lg')]: {
            textAlign: 'right',
            paddingTop: '14px'
        }
    },

    title: {
        fontSize: '26px',
        fontWeight: 'bold',
        color: '#3C4858',
        padding: '10px',
    },

    subTitle: {
        color: '#6c757d',
        fontWeight: 'bold',
        fontSize: '20px',
        padding: '10px',
    },

    p: {
        color: '#999',
        fontSize: '16px',
        padding: '0 10px',
    },

    hightlightImage: {
        width: '100%',
        boxShadow: '0 5px 15px -8px rgba(0, 0, 0, 0.24), 0 8px 10px -5px rgba(0, 0, 0, 0.2)',
        borderRadius: '6px',
        margin: '20px 0'
    },

    ul: {
        paddingTop: '20px',
        paddingBottom: '10px',
        listStyle: 'none',
        [theme.breakpoints.only('xs')]: {
            paddingLeft: '0px',
            textAlign: 'center'
        }
    },

    li: {
        padding: '5px 0',
        fontSize: '16px',
        color: ' #3c4858'
    },

    cursor: {
        cursor: 'pointer'
    },

    socialWrapper: {
        alignItems: 'center',
    },

    icon: {
        margin: '0px 10px',
        cursor: 'pointer',
    },

    youtube: {
        color: '#FF0000'
    },

    twitter: {
        color: '#55acee'
    },

    fb: {
        color: '#3b5998'
    },

    insta: {
        color: '#b900b4'
    },

    follow: {
        color: '#4caf50'
    },

    widget: {
        boxShadow: "0px 3px 3px -2px rgba(0,0,0,0.2), 0px 3px 4px 0px rgba(0,0,0,0.14), 0px 1px 8px 0px rgba(0,0,0,0.12)",
        borderRadius: "4px",
        backgroundColor: "#fff",
        transition: "box-shadow 300ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;",
        margin: "10px 10px 20px 10px",
        height: 'max-content'
    },
    scrollIcon: {
        fontSize: '38px',
        position: 'fixed',
        right: '0px',
        top: '120px',
        borderRadius: '50%',
        border: '1px solid',
        cursor: 'pointer'
    }
}));

const scrollToRef = (ref: any) => {
    if (ref.current) {
        window.scrollTo(0, ref.current.offsetTop - 60);
    }
}

const Profile: React.FC<ProfileProps> = () => {
    const classes = useStyles();

    const instaRef = useRef<HTMLDivElement | null>(null);
    const videoRef = useRef<HTMLDivElement>(null);
    const venueRef = useRef<HTMLDivElement>(null);
    const testimonialRef = useRef<HTMLDivElement>(null);

    let { username } = useParams();
    const { globalState } = useApplicationState();

    if (username === undefined || username === "undefined")
        return null;

    const bandId = getBandIdFromUsername(globalState.bandList, username);
    let info = globalState.bandList[bandId - 1];

    if (info === undefined || info === "undefined")
        return null;

      return (
        <div>
            <QuickNav instaRef={instaRef} videoRef={videoRef} venueRef={venueRef} testimonialRef={testimonialRef} />
            <Grid container>
                <Grid item xs={12} sm={3} lg={2} style={{ paddingTop: '14px' }}>
                    <div className={classes.imgWrapper}>
                        <a>
                            <img className={classes.img} src={info.logo} alt="..." />
                        </a>
                    </div>
                </Grid>
                <Grid item xs={12} sm={9} lg={7} style={{ paddingTop: '14px' }}>
                    <div className={classes.title}>{info.name}</div>
                    <div className={classes.subTitle}>{info.highlight}</div>
                </Grid>

                <Grid item xs={12} sm={12} lg={3} className={classes.btnWrapper}>
                    <div className={classes.socialWrapper}>
                        <InstagramIcon fontSize="large" className={clsx(classes.icon, classes.insta)} />

                        <YouTubeIcon fontSize="large" className={clsx(classes.icon, classes.youtube)} />
                        <FacebookIcon fontSize="large" className={clsx(classes.icon, classes.fb)} />
                        <AddCircleIcon fontSize="large" className={clsx(classes.icon, classes.follow)} />
                    </div>
                </Grid>

                <Grid item xs={12} md={8}>
                    <Grid container>
                        <Grid item xs={12}>
                            <p className={classes.p}>{info.description}</p>
                        </Grid>
                        <Grid item xs={12} sm={8}>
                            <img className={classes.hightlightImage} src="https://demos.creative-tim.com/material-kit-pro-react/static/media/card-project1.6857ca8c.jpg" alt="..." />
                        </Grid>
                        <Grid item xs={12} sm={4}>
                            <ul className={classes.ul}>
                                <li className={classes.li}><b>60</b> Gigs</li>
                                <li className={classes.li}><b>20</b> Venues</li>
                                <li className={classes.li}><b>331</b> Followers</li>
                                <li className={classes.li}><b>100</b> Likes</li>
                            </ul>
                        </Grid>
                    </Grid>

                </Grid>
                <Grid item xs={12} md={4}>
                    <InfoCardMedium title="Lyanna Mormont" subTitle="Guitarist"
                        img="https://66.media.tumblr.com/bb030d3ed2ae940d01678181cef3575f/tumblr_pqtmldMy651y6zsgxo2_250.png"
                        content="Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua"
                    />


                    <InfoCardMedium title="Petyr Baelish" subTitle="Keyboard / Vocals"
                        img="https://vignette.wikia.nocookie.net/gameofthrones/images/9/9f/Profile-Littlefinger.png/revision/latest/top-crop/width/360/height/360?cb=20170826005231"
                        content="Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua"
                    />

                    <InfoCardMedium title="Tyrion Lannister" subTitle="Vocals"
                        img="https://vignette.wikia.nocookie.net/gameofthrones/images/9/95/HandoftheKingTyrionLannister.PNG/revision/latest/top-crop/width/360/height/360?cb=20190520175204"
                        content="Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua"
                    />

                </Grid>
            </Grid>
            <div ref={instaRef} >
                <InstaPostContainer list={instaPostList} />
            </div>

            <div ref={videoRef} >
                <VideoPostContainer list={videoList} />
            </div>

            <div ref={venueRef} >
                <VenuePlayedContainer list={venueList} />
            </div>

            <div ref={testimonialRef} >
                <TestimonialContainer list={testimonialList} />
            </div>

        </div >
    )
}
export default Profile;