import React from "react";

import { makeStyles, Theme } from "@material-ui/core";

interface InfoCardSmallProps {
    img?: string,
    title: string,
    subTitle?: string,
    mirror?: boolean
}

const useStyles = makeStyles((theme: Theme) => ({
    root: {
        minHeight: '90px',
        margin: '10px 0',
    },

    imgWrapper: {
        width: '64px',
        height: '64px',
        margin: '0 15px',
        overflow: 'hidden',
        boxShadow: '0 6px 10px 0 rgba(0, 0, 0, 0.14), 0 1px 18px 0 rgba(0, 0, 0, 0.12), 0 3px 5px -1px rgba(0, 0, 0, 0.2)',
        borderRadius: '50%',
    },

    img: {
        width: '100%',
        height: 'auto',
    },

    floatLeft: {
        float: 'left'
    },

    floatRight: {
        float: 'right'
    },

    textAlignRight: {
        textAlign: 'right'
    },


    title: {
        marginTop: '6px',
        fontSize: '18px',
        color: '#3C4858',
        fontWeight: 'bold'
    },

    subTitle: {
        marginTop: '6px',
        fontSize: '14px',
        color: '#6c757d',
        fontWeight: 'bold'
    },

}));


const InfoCardSmall: React.FC<InfoCardSmallProps> = ({ img, title, subTitle, mirror }) => {

    const classes = useStyles();


    return (
        <div className={classes.root}>
            {img ?
                <a className={mirror ? classes.floatRight : classes.floatLeft}>
                    <div className={classes.imgWrapper}>
                        <img className={classes.img} src={img} alt="..." />
                    </div>
                </a> : null}

            <div className={mirror ? classes.textAlignRight : ''}>
                <div className={classes.title} >{title}</div>
                {subTitle ?
                    <div className={classes.subTitle}>{subTitle}</div>
                    : null}
            </div>
        </div>
    );
}

export default InfoCardSmall;