import React from "react";
import clsx from 'clsx';

import { makeStyles, Theme } from "@material-ui/core";

import InstagramIcon from '@material-ui/icons/Instagram';
import YouTubeIcon from '@material-ui/icons/YouTube';
import FacebookIcon from '@material-ui/icons/Facebook';


interface InfoCardProps {
    id: number,
    img?: string,
    title: string,
    subTitle?: string,
    content?: string,
    handleClick?: (id: number) => void
}

const useStyles = makeStyles((theme: Theme) => ({
    root: {
        textAlign: 'center',
        marginTop: '30px'
    },

    imgWrapper: {
        margin: 'auto',
        overflow: 'hidden',
        maxWidth: '130px',
        boxShadow: ' 0 16px 38px -12px rgba(0, 0, 0, 0.56), 0 4px 25px 0px rgba(0, 0, 0, 0.12), 0 8px 10px -5px rgba(0, 0, 0, 0.2)',
        height: '130px',
        borderRadius: '50%',
        cursor: 'pointer'
    },

    img: {
        width: '100%',
        height: 'auto',
    },

    details: {
        padding: '15px 30px'
    },

    socialWrapper: {
        alignItems: 'center',
        marginTop: '14px'
    },

    icon: {
        margin: '0px 10px',
        cursor: 'pointer',
    },

    youtube: {
        color : '#FF0000'
    },

    fb: {
        color: '#3b5998'
    },

    insta: {
        color: '#b900b4'
    },

    title: {
        marginTop: '6px',
        fontSize: '18px',
        color: '#3C4858',
        fontWeight: 'bold'
    },

    subTitle: {
        marginTop: '6px',
        fontSize: '14px',
        color: '#6c757d',
        fontWeight: 'bold'
    },

    p: {
        color: '#999'
    }

}));


const InfoCard: React.FC<InfoCardProps> = ({ id, img, title, subTitle, content, handleClick }) => {

    const classes = useStyles();

    const handleClickTitle = () => {
        if (handleClick)
            handleClick(id);
    }

    return (
        <div className={classes.root}>
            <div className={classes.imgWrapper} onClick={handleClickTitle}>
                {img ?
                    <a>
                        <img className={classes.img} src={img} alt="..." />
                    </a> : null}
            </div>
            <div className={classes.details}>
                <div className={classes.title} >{title}</div>
                {subTitle ?
                    <div className={classes.subTitle}>{subTitle}</div>
                    : null}
                {content ?
                    <p className={classes.p}>{content}</p> : null}

                <div className={classes.socialWrapper}>
                    <InstagramIcon className={clsx(classes.icon, classes.insta)} />
                    <YouTubeIcon className={clsx(classes.icon, classes.youtube)} />
                    <FacebookIcon className={clsx(classes.icon, classes.fb)} />
                </div>
            </div>
        </div >
    );
}

export default InfoCard;