import React, { useState, useRef } from "react";
import clsx from 'clsx';

import { makeStyles, Theme } from "@material-ui/core";
import Grid from "@material-ui/core/Grid";

import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import ExpandLessIcon from '@material-ui/icons/ExpandLess';

import VideoPlayer from './VideoPlayer';

interface InstaPostContainerProps {
    list: any
}

const useStyles = makeStyles((theme: Theme) => ({

    root: {
        boxShadow: "0px 3px 3px -2px rgba(0,0,0,0.2), 0px 3px 4px 0px rgba(0,0,0,0.14), 0px 1px 8px 0px rgba(0,0,0,0.12)",
        borderRadius: "4px",
        backgroundColor: "#fff",
        transition: "box-shadow 300ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;",
        margin: "20px 10px 40px 10px",
        height: 'max-content',
        padding: '20px 0',
        position: 'relative',
    },

    pulldown: {
        position: 'absolute',
        bottom: '-26px',
        textAlign: 'center',
        width: '100%',
    },

    pullDownIcon: {
        boxShadow: "0px 3px 3px -2px rgba(0,0,0,0.2), 0px 3px 4px 0px rgba(0,0,0,0.14), 0px 1px 8px 0px rgba(0,0,0,0.12)",
        transition: "box-shadow 300ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;",
        color: '#999',
        fontSize: '40px',
        cursor: 'pointer',
        border: '1px solid transparent',
        borderRadius: '50%',
        background: '#f6f6f6',
        '&:hover': {
            color: '#6c757d',
        }
    },

    pullUpIcon: {
        boxShadow: "0px 3px 3px -2px rgba(0,0,0,0.2), 0px 3px 4px 0px rgba(0,0,0,0.14), 0px 1px 8px 0px rgba(0,0,0,0.12)",
        transition: "box-shadow 300ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;",
        color: '#999',
        fontSize: '40px',
        cursor: 'pointer',
        border: '1px solid transparent',
        borderRadius: '50%',
        background: '#f6f6f6',
        position: 'sticky',
        bottom: '0px',

        '&:hover': {
            color: '#6c757d',
        }
    },

    stickyIcon: {
        marginRight: '10px',
        marginTop: '-22px',
        background: 'rgba(246,246,246,0.75)',
        float: 'right'
    },


    videoThumbnail: {
        padding: '10px'
    },

    img: {
        width: '100%',
        borderRadius: '4px',
        cursor: 'pointer',
    },

    gridContainer: {
        padding: '10px',
        [theme.breakpoints.only('xs')]: {
            padding: '0'
        }
    },

}));


const InstaPostContainer: React.FC<InstaPostContainerProps> = ({ list }) => {

    const classes = useStyles();
    const videoInit = list.length < 4 ? list.length : 4;
    const [videoListSize, setVideoListSize] = useState(videoInit);
    const [playerOpen, setPlayerOpen] = useState(false);
    const [selectedVideo, setVideo] = useState();
    const ref = useRef<HTMLDivElement | null>(null);

    const items = [];

    const handlePulldown = () => {
        if (videoListSize + videoInit <= list.length) {
            setVideoListSize(videoListSize + videoInit);
        } else {
            setVideoListSize(list.length);
        }
    }

    const handlePullup = () => {
        setVideoListSize(videoInit);
        if (ref.current) {
            window.scrollTo(0, ref.current.offsetTop - 60);
        }
    }

    const showHidePlayer = () => {

        setPlayerOpen(!playerOpen);
    }

    const setVideoPlayer = (item: any) => {
        setVideo(item);
        setPlayerOpen(true);
    }

    for (let i = 0; i < videoListSize; i++) {
        let item = list[i];
        items.push(
            <Grid className={classes.videoThumbnail} xs={12} sm={6} md={4} lg={3} >
                <img className={classes.img} src={item.thumbnailUrl}
                    onClick={() => setVideoPlayer(item)}
                />
            </Grid>)
    }

    return (
        <div ref={ref} className={classes.root}>

            <Grid container className={classes.gridContainer}>
                {items}
            </Grid>
            <div className={classes.pulldown}>
                {(list.length === items.length) ?
                    <ExpandLessIcon className={classes.pullUpIcon} onClick={handlePullup} /> :
                    <ExpandMoreIcon className={classes.pullDownIcon} onClick={handlePulldown} />
                }
            </div>
            {selectedVideo ?
                <VideoPlayer open={playerOpen} handleClose={showHidePlayer} info={selectedVideo} /> : null
            }

            {(items.length !== videoInit && items.length !== list.length) ?
                <ExpandLessIcon className={clsx(classes.pullUpIcon, classes.stickyIcon)} onClick={handlePullup} /> : null}

        </div>
    );
}

export default InstaPostContainer;