import createMuiTheme from "@material-ui/core/styles/createMuiTheme";

declare module '@material-ui/core/styles/createMuiTheme' {
    interface Theme {
        button: {
            green: {
                main: string,
                hover: string
            }
        }
    }
    interface ThemeOptions {
        button: {
            green: {
                main: string,
                hover: string
            }
        }
    }
}
const theme = createMuiTheme({
    typography: {
        fontFamily: 'Roboto, sans-serif'
    },
    palette: {
        primary: {
            main: 'rgb(65, 101, 235)',
            //   contrastText: 'rgb(54, 19, 72)'
        },
        text: {
            // primary: '#6a6a6a',
            secondary: '#8a8a8a'
        },
        background: {
            default: '#f6f6f6',
            paper: '#fff',
        },
        error: {
            main: '#e74c3c',
            contrastText: '#fff'
        }
    },
    button: {
        green: {
            main: "rgba(0,173,0,1)",
            hover: "rgba(0,128,0,1)"
        },
    },

    overrides: {
      

    }
});

export default theme;
