import React from "react";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import { makeStyles, Theme, Container } from "@material-ui/core";
import useMediaQuery from '@material-ui/core/useMediaQuery';
import { useTheme } from '@material-ui/core/styles';
import Nebula from './containers/Nebula';
import Profile from './containers/Profile';
import Header from './components/Header';



interface AppProps { }

const useStyles = makeStyles((theme: Theme) => ({
    root: {
        marginTop: '50px',
    },

    serviceWrapper: {
        display: 'flex'
    },

    service: {
        fontSize: '22px',
        letterSpacing: '.22px',
        paddingBottom: '10px',
        paddingLeft: '10px',
        fontWeight: 'bold',
        color: '#3e3e3e'
    },

    footer: {
        padding: '15px 0',
        position: 'relative',
        textAlign: 'center'
    }


}));

const App: React.FC<AppProps> = () => {
    const classes = useStyles();

    const matches = useMediaQuery(useTheme().breakpoints.only('xs'));

    return (
        <Container maxWidth="xl" className={classes.root} disableGutters={matches}>
            <Router>
                <Header />
                <Switch>
                    <Route path="/:username">
                        <Profile />
                    </Route>
                    <Route path="/">
                        <Nebula />
                    </Route>
                </Switch>
                <footer className={classes.footer}>
                    <div>Copyright © 2020 BackStageConnect All Rights Reserved.</div>
                </footer>
            </Router>
        </Container>
    )
}
export default App;